import React, { useEffect, useRef, useState } from 'react';
import { useGLTF, Text } from '@react-three/drei';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import FlipOne from './flip1';
import { Box3, Vector3 } from 'three';
import { useFrame } from '@react-three/fiber';

export function Hole(props) {
  const { nodes, materials } = useGLTF(process.env.PUBLIC_URL + '/newhole.glb');
  const rotationRef = useRef();
  const ref = useRef();
  const group = useRef();
  gsap.registerPlugin(ScrollTrigger);
  useEffect(() => {
    if (ref.current && group.current) {
      ref.current.geometry.center();

      gsap.to(group.current.rotation, {
        z: -Math.PI,
        scrollTrigger: {
          start: 'top top',
          end: 'bottom bottom',
          scrub: true,
        },
      });
    }
  }, [ref]);

  return (
    <group ref={group} {...props} dispose={null}>
      <group>
        <group>
          <FlipOne
            setText={props.setText}
            url={process.env.PUBLIC_URL + '/flip-0.mp4'}
          />
        </group>

        <group rotation-z={Math.PI}>
          <FlipOne
            setText={props.setText}
            url={process.env.PUBLIC_URL + '/flip-2.mp4'}
            rotation-z={-Math.PI}
          />
        </group>

        <mesh
          scale={1.4}
          ref={ref}
          castShadow
          receiveShadow
          geometry={nodes.Cube002.geometry}
          material={nodes.Cube002.material}
        />
      </group>
    </group>
  );
}

useGLTF.preload(process.env.PUBLIC_URL + '/newhole.glb');
