import { Canvas } from '@react-three/fiber';
import { Floor } from './3D/Floor';
import { Hole } from './3D/Hole';
import './styles/index.css';

function App() {
  return (
    <Canvas
      dpr={[1, 2]}
      camera={{
        position: [0, 0, 13.5],
        fov: 75,
        far: 100,
      }}
      shadows
      className='canvas'
      gl={{ antialias: false }}
      style={{ position: 'fixed' }}
    >
      <group>
        <ambientLight color={0xffff} intensity={1} />
        <directionalLight intensity={2.5} color={0x222625} />
        <Floor
          scale={2}
          rotation={[0.5, -Math.PI * 2, 0]}
          position-z={9}
          position-y={-2}
        />

        <Hole
          scale={1.5}
          rotation={[0.5, 0, 0]}
          position-y={-2.1}
          position-z={8.9}
        />
      </group>
    </Canvas>
  );
}

export default App;

